import { defineStore } from 'pinia'
import { MessageBox, Message } from 'element-ui'
import _ from 'lodash'

import {
  index as indexPages,
  show as showPage,
  store as storePage,
  update as updatePage,
  destroy as deletePage,
  updateContent
} from '@/api/pages/index'

import { useStoreConfig } from '../configStore'

const configStore = useStoreConfig()

export const useStorePage = defineStore('page', {
  state: () => ({
    configStore,
    isEdit: false,
    isFile: true,
    isSubNode: false,
    isFormVisible: false,
    loading: false,
    dialogVisible: false,
    dialogVisibleNode: false,
    contentHtml: '',
    keyName: '',
    form: {},
    formNode: {},
    formPage: {},
    list: [],
    listPages: null,
    data: null,
    node: null,
    file: {
      name: '',
      pathFile: '',
      type: '',
      order: 0
    },
    isVisibleNavBar: true,
    dialogTitle: '',
    isJavascript: false,
    editor: {},
    headerOptions: [],
    footerOptions: [],
    mainOptions: [],
    templateNode: {
      keyNamePage: '',
      menuTitle: 'Nueva página',
      slug: '',
      keyNameTemplateMain: '',
      keyNameTemplateHeader: '',
      keyNameTemplateFooter: '',
      type: 'page',
      urlRedirec: 'http://',
      target: '_self',
      isVisible: true
    },
    templatePage: {
      keyName: '',
      menuTitle: '',
      lang: '',
      content: ''
    },
    targetOptions: [
      {
        label: 'Misma ventana o tab',
        value: '_self'
      },
      {
        label: 'Nueva ventana o tab',
        value: '_blank'
      },
      {
        label: 'Ventana padre',
        value: '_parent'
      },
      {
        label: 'Ventana principal',
        value: '_top'
      }
    ],
    typeOptions: [
      {
        label: 'Página',
        value: 'page'
      },
      {
        label: 'Redirección',
        value: 'redirect'
      }
    ]
  }),
  actions: {
    async indexPages () {
      this.loading = true
      await indexPages()
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }
          Message({
            showClose: true,
            message: response.messages[0] || 'Exito',
            type: 'success'
          })

          this.listPages = response.data

          return this.listPages
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
        .finally(() => (this.loading = false))
    },
    async showPage (keyName) {
      this.loading = true
      await showPage(keyName)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }
          Message({
            showClose: true,
            message: response.messages[0] || 'Exito',
            type: 'success'
          })

          this.form = response.data

          return this.form
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
        .finally(() => (this.loading = false))
    },
    async storePage (pageData) {
      this.loading = true
      return await storePage(pageData)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }
          Message({
            showClose: true,
            message: response.messages[0] || 'Exito',
            type: 'success'
          })

          this.form = response.data

          return this.form
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
        .finally(() => (this.loading = false))
    },
    async updatePage (pageData, keyName) {
      this.loading = true
      await updatePage(pageData, keyName)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }
          Message({
            showClose: true,
            message: response.messages[0] || 'Exito',
            type: 'success'
          })

          this.form = response.data

          return this.form
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
        .finally(() => (this.loading = false))
    },
    async deletePage (keyName) {
      this.loading = true
      await deletePage(keyName)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }
          Message({
            showClose: true,
            message: response.messages[0] || 'Exito',
            type: 'success'
          })

          this.form = response

          return this.form
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
        .finally(() => (this.loading = false))
    },
    async updateContentHtml (dataEditor, keyName) {
      return await updateContent({ content: dataEditor }, keyName)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }
          Message({
            showClose: true,
            message: response.messages[0] || 'Exito',
            type: 'success'
          })

          return response.data
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
    },

    // Dialog Menu
    handleCreateMenu (index, row) {
      this.isEdit = false
      this.dialogVisible = true
      this.form = _.cloneDeep(this.templateBlank)
    },
    handleCreateNode (index, row) {
      this.form = _.cloneDeep(this.templateBlank)
    },
    handleClose (done) {
      MessageBox.confirm('Estas deacuerdo en cerrar la ventana?')
        .then((_) => {
          done()
        })
        .catch((_) => {})
    },
    showNavBar () {
      this.isVisibleNavBar = !this.isVisibleNavBar
    }
  }
})
