import request from '@/utils/request'

export function get (keyName) {
  return request({
    url: 'pages/' + keyName + '/css',
    method: 'get'
  })
}

export function index (keyName) {
  return request({
    url: 'pages/' + keyName + '/css/files',
    method: 'get'
  })
}

export function show (keyName, id) {
  return request({
    url: '/pages/' + keyName + '/css/files/' + id,
    method: 'get'
  })
}

export function update (data, keyName, id) {
  return request({
    url: '/pages/' + keyName + '/css/files/' + id,
    method: 'put',
    data
  })
}

export function updateAll (data, keyName) {
  return request({
    url: '/pages/' + keyName + '/css/files/all',
    method: 'put',
    data
  })
}

export function store (data, keyName) {
  return request({
    url: '/pages/' + keyName + '/css/files',
    method: 'post',
    data
  })
}

export function destroy (keyName, id) {
  return request({
    url: '/pages/' + keyName + '/css/files/' + id,
    method: 'delete'
  })
}

export function indexCustom (keyName) {
  return request({
    url: '/pages/' + keyName + '/css/custom',
    method: 'get'
  })
}

export function updateCustom (data, keyName) {
  return request({
    url: '/pages/' + keyName + '/css/custom',
    method: 'put',
    data
  })
}
