import { defineStore } from 'pinia'
import { Message } from 'element-ui'

import {
  index as getJavascript,
  store as storeJavascript,
  updateAll as updateJavascript,
  destroy as deleteJavascript,
  indexCustom as getCustomJavascript,
  updateCustom as updateCustomJavascript
} from '@/api/pages/files/javascript'

import { useStorePage } from '../pageStore'

const pageStore = useStorePage()

export const useStoreJavaScript = defineStore('javascript', {
  state: () => ({
    isEdit: false,
    pageStore,
    // keyNameTemplate: '',
    // dialogVisible: false,
    content: '',
    loading: false,
    list: null,
    fileTemplateBlank: {
      name: '',
      pathFile: '',
      type: '',
      order: 0
    },
    file: {
      name: '',
      pathFile: '',
      type: '',
      order: 0
    }
  }),
  actions: {
    // JavaScript
    async fetchJavascript (keyName) {
      pageStore.loading = true
      return await getJavascript(keyName)
        .then((response) => {
          this.list = response.data

          return this.list
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
        .finally(() => (pageStore.loading = false))
    },
    async storeJavascriptFile (fileData, keyName) {
      // pageStore.loading = true
      // const fileData = _.cloneDeep(this.file)
      return await storeJavascript(fileData, keyName)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error!',
              type: 'warning'
            })

            return false
          }

          Message({
            showClose: true,
            message: response.messages[0] || 'Exito!',
            type: 'success'
          })

          this.list = response.data

          return this.list
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
      // .finally(() => (pageStore.loading = false))
    },
    async updateJavascriptFiles (listData, keyName) {
      pageStore.loading = true
      return await updateJavascript({ files: listData }, keyName)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error!',
              type: 'warning'
            })

            return false
          }
          Message({
            showClose: true,
            message: response.messages[0] || 'Exito!',
            type: 'success'
          })

          this.list = response.data

          return this.list
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
        .finally(() => (pageStore.loading = false))
    },
    async deleteJavascriptFile (keyName, id) {
      pageStore.loading = true
      return deleteJavascript(keyName, id)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error!',
              type: 'warning'
            })

            return false
          }

          Message({
            showClose: true,
            type: 'success',
            message: response.messages[0] || 'Exito!'
          })

          this.list = response.data

          return this.list
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
        .finally(() => (pageStore.loading = false))
    },
    orderFilesJavascript () {
      this.list.forEach((el, index) => {
        el.order = index + 1
      })
    },
    async fetchCustomJavascript (keyName) {
      pageStore.loading = true
      return await getCustomJavascript(keyName)
        .then((response) => {
          this.content = response.data.content

          return this.content
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
        .finally(() => (pageStore.loading = false))
    },
    async updateCustomJavascript (customData, keyName) {
      pageStore.loading = true
      return await updateCustomJavascript({ content: customData }, keyName)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error!',
              type: 'warning'
            })

            return false
          }

          Message({
            showClose: true,
            message: response.messages[0] || 'Exito!',
            type: 'success'
          })
          this.content = response.data.content

          return this.content
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
        .finally(() => (pageStore.loading = false))
    }
  }
})
