import { defineStore } from 'pinia'
import { Message } from 'element-ui'
// import _ from 'lodash'

import {
  index as getCss,
  store as storeCss,
  updateAll as updateCss,
  destroy as deleteCss,
  indexCustom as getCustomCss,
  updateCustom as updateCustomCss
} from '@/api/pages/files/css'

import { useStorePage } from '../pageStore'

const pageStore = useStorePage()

export const useStoreCss = defineStore('css', {
  state: () => ({
    pageStore,
    isEdit: false,
    loading: false,
    content: '',
    list: [],
    fileTemplateBlank: {
      name: '',
      pathFile: '',
      type: '',
      order: 0
    },
    file: {
      name: '',
      pathFile: '',
      type: '',
      order: 0
    }
  }),
  actions: {
    async fetchCss (keyName) {
      pageStore.loading = true
      return await getCss(keyName)
        .then((response) => {
          // console.log('~ response', response)
          this.list = response.data

          return this.list
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
        .finally(() => (pageStore.loading = false))
    },
    async storeCssFile (fileData, keyName) {
      return await storeCss(fileData, keyName)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error!',
              type: 'warning'
            })

            return false
          }

          Message({
            showClose: true,
            message: response.messages[0] || 'Exito!',
            type: 'success'
          })

          this.list = response.data

          return this.list
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
    },
    async updateCssFiles (cssData, keyName) {
      pageStore.loading = true
      return await updateCss({ files: cssData }, keyName)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error!',
              type: 'warning'
            })

            return false
          }

          Message({
            showClose: true,
            message: response.messages[0] || 'Exito!',
            type: 'success'
          })

          this.list = response.data

          return this.list
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
        .finally(() => (pageStore.loading = false))
    },
    async deleteCssFile (keyName, id) {
      pageStore.loading = true
      return await deleteCss(keyName, id)
        .then((response) => {
          // console.log('~ response', response)
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error!',
              type: 'warning'
            })

            return false
          }

          Message({
            showClose: true,
            message: response.messages[0] || 'Exito!',
            type: 'success'
          })

          this.list = response.data

          return this.list
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
        .finally(() => (pageStore.loading = false))
    },
    orderFilesCss () {
      this.list.forEach((el, index) => {
        el.order = index + 1
      })
    },
    async fetchCustomCss (keyName) {
      pageStore.loading = true
      return await getCustomCss(keyName)
        .then((response) => {
          // console.log(response)
          this.content = response.data.content

          return this.content
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
        .finally(() => {
          pageStore.loading = false
        })
    },
    async updateCustomCss (customData, keyName) {
      pageStore.loading = true
      return await updateCustomCss({ content: customData }, keyName)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error!',
              type: 'warning'
            })

            return false
          }

          Message({
            showClose: true,
            message: response.messages[0] || 'Exito!',
            type: 'success'
          })

          this.content = response.data.content

          return this.content
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
        .finally(() => (pageStore.loading = false))
    }
  }
})
